import {api} from '/js/api';

api({
	getUnreadList: 'message/GetMessageByCurrentUser', //0未读，1已读，其他为全部
	getReadList: 'message/GetMessageByCurrentUser', //0未读，1已读，其他为全部
	setRead: 'message/UpMessageStatusByMsgId?status=1', //1 已读
});

let render = template.compile(document.getElementById('tpl-msg').innerHTML);

api.getUnreadList({status: 0}, data => {
	document.getElementById('ulUnread').innerHTML = render(data);
});
api.getReadList({status: 1}, data => {
	document.getElementById('ulRead').innerHTML = render(data);
});


$('#divUnread').on('tap' , '.mui-navigate-right' , function () {
	api.setRead({msgId : this.getAttribute('data-id')})
});